*, *:before, *:after
{
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}

html, body, #root {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  font-family: sans-serif;
  background-color: #000;

  font-size: 6.5vh;
}



